#blobCloud {
    transform: translate(-4vw, -10vw);
    margin-top: 10vh;
    //opacity: 0.50;
    filter: blur(0.5px);
    position: absolute;
    right: 1vw;
    top: 30vw;
    top: calc(60% - 30vmin);
    left: calc(85% - 30vmin);
    display: block;
    width: 23%;

}

.softSkills {
    display: flex;
    flex-direction: column;
    color: rgba(47, 51, 86, 0.871);

    &__Intro {
        font-weight: 800;
        font-size: 22px;

        .IntroP {
            font-weight: 800;
        }

    }


    &__p {
        font-size: 15px;
        margin-bottom: 22px;
    }

    &__item {
        display: flex;
        flex-direction: row;
        font-weight: 500;
    }

    &__title {
        // color: rgb(131, 135, 153);
        font-weight: 400;
        font-size: 15px;

    }

    .fa {
        display: block;
        width: 20px;
        height: 20px;
        margin-right: 15px;
        color: rgba(43, 43, 48, 0.871);
    }

    .ButtonCV {
        margin-left: 80px;
    }


}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}