@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import './setting.scss';

:root {

    //white-space: pre-line;
    --body_background: $couleur-body;
    //--body_background: $couleur-body;
    --body_color: #f3ebf6;
    --link_color: navy;
    --box_background: linear-gradient(#e9d3f1, #d4a9d6);
    --spark: 1.8s;
}

[data-theme="dark"] {
    --body_background: rgb(24, 2, 35);
    --body_color: #f3ebf6;
    --body_background: #f3ebf6;
    --link_color: white;
    --lang_color: #695da1;
    --lang_background: #433871;
    --background_icons: #c996cc;
    --box-shadow: 8px 8px 8px 6px rgba(255, 255, 255, 0.327), -4px -4px 6px 0 rgba(116, 125, 136, 0.5), inset -4px -4px 6px 0 rgba(255, 255, 255, 0.2), inset 4px 4px 6px 0 rgba(0, 0, 0, 0.4);
    --box-shadow-portfolio: 5px 10px 18px rgba(0, 0, 0, 0.8);
    --skill_color: #e7d2ee
}

[data-theme="dark"] {
    --body_background: rgba(16, 7, 51, 1);
    --box_background: linear-gradient(#8552c4, #916bbf);
    background: rgb(131, 58, 180);
    --background: linear-gradient(90deg, rgba(131, 58, 180, 1) 0%, rgba(253, 250, 29, 0.8352591036414566) 50%, rgba(252, 176, 69, 1) 100%); //nouvea
    --body_color: #f3ebf6;
    --lang_color: #695da1;
    --lang_background: rgb(67, 56, 113);
    --background_icons: #c996cc;
    --hover: linear-gradient(45deg,
            #645cbb,
            #1c0c5b,
            #ebc7e6,
            #3d2c8d,
            #bface2,
            #7a00ff,
            #fbf4f9,
            #a084dc)
}

* {

    padding: 0;
    margin: 0;
    //  background-color: #e9e2e2;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
    background-color: var(--body_background);
    color: var(--link_color);
    transition: all 0.3s ease-in-out;
}

html {
    height: 100%;
}

body {
    @include body-position;
    overflow-x: hidden;
    color: var(--body_color);
    background-color: var(--body_background);
    margin: 0 auto;
    color: var(--link_color);
    height: 100%;
    font-family: "Open Sans", sans-serif;
    -webkit-font-smoothing: antialiased;
    /*
 @media (min-width: $breakpoint-desktop) {
        max-width: 100%;
        background-color: $body-background;
    }
    
    body{font-family:"Open Sans",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;max-width:1600px;font-size:16px}
    
    */

}

.App {
    position: relative;
    min-height: 100vh;
    flex-grow: 1;
}