.dark_mode {
    margin-top: -20px;
    margin-left: 10px;
    position: absolute;
    right: 10%;
    top: 0.95%;
}

.dark_mode_label {
    width: 50px;
    height: 26px;
    position: relative;
    display: block;
    background: #ebebeb;
    border-radius: 200px;
    box-shadow: inset 0 5px 15px rgba(0, 0, 0, .4), inset 0 -5px 15px rgba(255, 255, 255, .4);
    cursor: pointer;
    transition: .3s
}

.dark_mode_label:after {
    content: "";
    width: 20px;
    height: 20px;
    margin-left: 3px;
    margin-top: 2.5px;
    position: absolute;
    top: 1.2%;
    background: linear-gradient(180deg, #ffcc89, #d8860b);
    border-radius: 180px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
    transition: .3s
}

.dark_mode_input {
    width: 0;
    height: 0;
    visibility: hidden;
}

.dark_mode_input:checked+.dark_mode_label {
    background: #242424
}

.dark_mode_input:checked+.dark_mode_label:after {
    left: 62px;
    transform: translateX(-100%);
    background: linear-gradient(180deg, #777, #3a3a3a)
}

.dark_mode_label:active after {
    width: 30px;

}

.dark_mode_label svg {
    position: absolute;
    width: 15px; //
    top: 5px;
    z-index: 100;
}



.dark_mode_label svg.sun {
    left: 5px;
    top: 5px;
    fill: #fff;
    transition: .3s
}

.dark_mode_label svg.moon {
    left: 30px;
    fill: #7e7e7e;
    transition: .3s
}

.dark_mode_input:checked+.dark_mode_label svg.sun {
    fill: #7e7e7e;

}

.dark_mode_input:checked+.dark_mode_label svg.moon {
    fill: #fff;
}