.biographie__container {
    color: var(--body_color);
    width: 55%;


    &_h4 {
        width: 55%;
        margin-top: 20px;
        margin-bottom: 22px;
        margin-left: 40px;
        animation: script 12s steps(45) infinite;
    }
    &__p {
        display: block;
        margin-left: 40px;
        word-spacing: 6px;
    }
}



