@keyframes script {
    0% {
        width: 0
    }

    45% {
        width: 102%
    }

    55% {
        width: 102%
    }

    100% {
        width: 0
    }
}

@media (max-width:767px) {}

@media (max-width:450px) {
    p {
        font-size: 14px;
        padding: 0 5px
    }
}